  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 60px;
  top: 0;
  left: 0;
  background-color: rgba(255, 160, 122, 0.8);
  z-index: 10; }

.navigation__toggle {
  position: absolute;
  top: 10px;
  right: 15px;
  width: 35px;
  height: 40px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  opacity: 0.8;
  transition: 0.3s ease-in; }
  .navigation__toggle:hover {
    opacity: 1;
    transition: 0.3s ease-in; }

.navigation--closed .navigation__toggle::before {
  position: absolute;
  top: 6px;
  right: 0;
  content: "";
  height: 4px;
  width: 35px;
  background-color: #2F4F4F;
  box-shadow: 0 12px 0 0 #2F4F4F, 0 24px 0 0 #2F4F4F; }

.navigation--opened .navigation__toggle::after, .navigation--opened .navigation__toggle::before {
  content: "";
  position: absolute;
  top: 18px;
  right: 0px;
  width: 35px;
  height: 4px;
  background-color: #2F4F4F; }

.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg); }

.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg); }

.navigation--closed .navigation__list {
  display: none; }

.navigation--opened {
  background-color: lightsalmon; }

.navigation--opened .navigation__list {
  display: block;
  margin-top: 60px; }
  .navigation--opened .navigation__list::after {
    content: "";
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translateX(-50%);
    height: 5px;
    width: 35px;
    background-color: #2F4F4F; }

.navigation__item {
  width: 100%; }

.navigation__link {
  display: block;
